import axios from "axios";
import store from "@/store/store";
import { getToken } from "@/utils/auth";
import { logout } from "@/store/store/actions";

//axios
const service = axios.create({
  baseURL: 'http://127.0.0.1:5000',//process.env.REACT_APP_BASE_API, // api  base_url
  timeout: 5000, // request timeout
});

// 
service.interceptors.request.use(
  (config) => {
    // Do something before request is sent
    const token = getToken()
    if (token) {
      // token-- ['Authorization']key 
      config.headers.Authorization = `Bearer ${token}`
    }
    return config;
  },
  (error) => {
    // Do something with request error
    console.log(error); // for debug
    Promise.reject(error);
  }
);

// 
service.interceptors.response.use(
  // (response) => response,
  /**
   * responsecode
   * code
   *  xmlhttprequest  error
   * 
   */
  response => {
    const res = response.data
    if (!res.status) {

      // message.error(res.message, 3)
      // 50008:token; 50012:;  50014:Token ;
      if (res.statusCode === 401 || res.statusCode === 403 || res.statusCode === 50008 || res.statusCode === 50012 || res.statusCode === 50014) {
        // Moda.confirm(
        //   {
        //     title: '',
        //     okText: '',
        //     cancelText: ''
        //   }
        // )
        store.dispatch(logout());
        // Modal.confirm('', '', {
        //   confirmButtonText: '',
        //   cancelButtonText: '',
        //   type: 'warning'
        // }).then(() => {
        //   store.dispatch('FedLogOut').then(() => {
        //     // location.reload() // vue-router bug
        //   })
        // })
      }
      return Promise.reject('error')
    } else {
      return response.data
    }
  },
  (error) => {
    console.log("err" + error); // for debug
    const { status } = error.response;
    if (status === 401 || status === 403) {
      // Modal.confirm({
      //   title: "?",
      //   content:
      //     "",
      //   okText: "",
      //   cancelText: "",
      //   onOk() {
      //     let token = store.getState().user.token;
      //     store.dispatch(logout(token));
      //   },
      //   onCancel() {
      //     console.log("Cancel");
      //   },
      // });
    }
    return Promise.reject(error);
  }
);

export default service;
